export var appConfig = {
    IsSaveStateToLocalStorage: true,
    IsLoadStateFromLocalStorage: false,
    SaveStateToLocalStorageInterval: 1000,
    Actions: {
        Types: {
            APP_SET_CALLBACK: 'APP_SET_CALLBACK',
            CARDBOARD_ADD: 'CARDBOARD_ADD',
            CARDBOARD_DELETE: 'CARDBOARD_DELETE',
            CARDBOARD_UPDATE: 'CARDBOARD_UPDATE',
            CARD_ADD: 'CARD_ADD',
            CARD_DELETE: 'CARD_DELETE',
            CARD_UPDATE: 'CARD_UPDATE',
        },
        SetCallback: function (callback) {
            return {
                type: appConfig.Actions.Types.APP_SET_CALLBACK,
                payload: callback,
            };
        },
        CardboardAdd: function (newCardboard) {
            return {
                type: appConfig.Actions.Types.CARDBOARD_ADD,
                payload: newCardboard,
            };
        },
        CardboardDelete: function (cardboardId) {
            return {
                type: appConfig.Actions.Types.CARDBOARD_DELETE,
                payload: cardboardId,
            };
        },
        CardboardUpdate: function (cardboardId, values) {
            return {
                type: appConfig.Actions.Types.CARDBOARD_UPDATE,
                payload: { cardboardId: cardboardId, values: values },
            };
        },
        CardAdd: function (cardboardId, newCard) {
            return {
                type: appConfig.Actions.Types.CARD_ADD,
                payload: { cardboardId: cardboardId, newCard: newCard },
            };
        },
        CardDelete: function (cardboardId, cardId) {
            return {
                type: appConfig.Actions.Types.CARD_DELETE,
                payload: { cardboardId: cardboardId, cardId: cardId },
            };
        },
        CardUpdate: function (cardboardId, cardId, values) {
            return {
                type: appConfig.Actions.Types.CARD_UPDATE,
                payload: { cardboardId: cardboardId, cardId: cardId, values: values },
            };
        },
    },
    PrimitiveTypes: {
        String: 'string',
        Number: 'number',
        Boolean: 'boolean',
    },
    InitialStateConfig: {
        ProjectName: 'New Project',
        ProjectId: 'New Project',
        ProjectRootName: 'New Project',
        SystemNamespace: 'System',
    },
    SystemFunctionNames: {
        Concat_String: 'Concat_String',
    },
    SystemFunctions: {
        Concat_String: function (Prefix, Separator, Postfix) {
            return "" + Prefix + Separator + Postfix;
        }
    }
};



